import React from "react";
import ParkingSpace from "../../ParkingSpace";
import PaymentScreen from "../../PaymentScreen";
import { defineFeature, loadFeature } from "jest-cucumber";
import { fireEvent, render, waitFor, act } from "@testing-library/react";
import "text-encoding";
import parkingSlice, {
  initializeParkingLot,
  addToParkingSpace,
  occupyParkingLots,
} from "../../redux/reducer/parkingSlice";
import "@testing-library/jest-dom/extend-expect";
import { Provider } from "react-redux";
import { configureStore } from "@reduxjs/toolkit";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  MemoryRouter,
  useNavigate,
} from "react-router-dom";
const feature = loadFeature("src/components/features/paymentscreen.feature");
let store: any;
let screen: any;
let occupiedSpaceId: number;
defineFeature(feature, (test) => {
  test("User pays for occupied parking spot", ({ given, when, then, and }) => {
    store = configureStore({
      reducer: {
        parkingSpaces: parkingSlice,
      },
    });
    const screen = render(
      <Provider store={store}>
        <MemoryRouter>
          <PaymentScreen />
        </MemoryRouter>
      </Provider>
    );
    const navigate = useNavigate();
    given("I am on the Payment Screen", () => {
      expect(screen).toBeTruthy();
    });
    and(
      "I see the vehicle registration and hours parked for the occupied parking spot",
      () => {
        store = configureStore({
          reducer: {
            parkingSpaces: parkingSlice,
          },
        });
        const screen = render(
          <Provider store={store}>
            <MemoryRouter initialEntries={["/paymentscreen"]} initialIndex={0}>
              <PaymentScreen />
            </MemoryRouter>
          </Provider>
        );
      }
    );
  });
});
